export default {
	data() {
		return {
			num: 1,
			numShow: false,
			quanShow:false
		};
	},
	methods: {
		showNum() {
			this.numShow = !this.numShow;
		},
		showquan() {
			this.quanShow = !this.quanShow;
		},
		setNum(i){
			if(i==1&&this.num>1){
				this.num--
			}
			if(i==2){
				this.num++
			}
		},
		setNumY(event){
			 let value = event.target.value;
			if(value>1){
				this.num = value
			}else{
				this.num = 1
			}
		}
	}
};
